<script>
  export default {
    data () {
      return {
        activeName:"1",
        input5:"",
        list:[1,2,3,4],
        pageLoad:false,

        //分类列表
        tags:[],

        //精选九大类课程
        TagCurriculum:{
          list:[],
          load:false
        },

        //推荐课程
        Recommendcourses:{
          list:[],
          load:false
        },
        //最新上线
        LatestOnline:{
          list:[],
          load:false
        },
        //推荐老师
        teacher:{
          list:[],
          load:false
        }



      }
    },
    methods:{
      go(e){
        this.$router.push(e)
      },
      handleClick(e){
      },
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      },

      //分类列表
      getTags(){
        this.$ajax("?i=1&c=entry&do=ymxh_sort&m=dg_chat&op=one",{
        })
          .then(response =>{
              this.tags=response.data.data;
              this.getTagCurriculum(this.tags[0].pid)
          })
      },

      //精选九大类课程
      getTagCurriculum(id){
        this.TagCurriculum.load=true;
        this.$ajax("?i=1&c=entry&do=ymxh_index_new&m=dg_chat",{
          params:{
            op:"jingxuan",
            tags_id:id,
          }
        })
          .then(response =>{
            this.TagCurriculum.list=response.data.data;
            this.TagCurriculum.load=false
          })
          .catch(error=>{
            this.TagCurriculum.load=false
          });
      },

      //推荐课程
      getRecommendcourses(page){  //获取推荐课程
        this.Recommendcourses.load=true;
         this.$ajax("?i=1&c=entry&do=ymxh_recommend&m=dg_chat&op=courses",{
           params :{
               type:"huan",
//               page:page,
               ping:'hot'
             }
         })
         .then(response =>{
           this.Recommendcourses.list=response.data.data;
           this.Recommendcourses.load=false
         })
         .catch(error=>{
           this.Recommendcourses.load=false
         });
      },

      //最新上线
      getLatestOnline(){
        this.LatestOnline.load=true;
        this.$ajax("?i=1&c=entry&do=ymxh_index_new&m=dg_chat",{
          params:{
            op:"list",
            tags_id:1
          }
        })
          .then(response =>{
            this.LatestOnline.list=response.data.data;
            this.LatestOnline.load=false;
          })
          .catch(error=>{
            this.LatestOnline.load=false;
          });
      },

      //推荐老师
      getteacher(){  //获取推荐课程
        this.teacher.load=true;
        this.$ajax("?i=1&c=entry&do=ymxh_recommend&m=dg_chat&op=teachers",{
          params:{
            type:"huan",
//            page:0,
          }
        })
          .then(response =>{
            this.teacher.list=response.data.data;
            this.teacher.load=false
          })
          .catch(error=>{
            this.teacher.load=false
          });
      },
    },
    mounted(){
      this.getLatestOnline();
      this.getRecommendcourses(); //推荐课程
      this.getteacher();  //推荐老师
      this.getTags(); //获取九大分类

    },
  }
</script>


